import React, { useState } from "react";
import { CopyToClipboard } from 'react-copy-to-clipboard';



export default function TextForm(props) {

    // Upper case Code
    const uppercase = () => {
        let NewText = text.toUpperCase();
        setText(NewText);
    }
    // lower case code 
    const lowercase = () => {
        let NewText = text.toLowerCase();
        setText(NewText);
    }
    // Clear all
    const Clearall = () => {
        let NewText = '';
        setText(NewText);
    }
    // remove Extra Space 
    const Extraspace = () => {
        let NewText = text.split(/[ ]+/);
        setText(NewText.join(" "));
    }

    const handleOnchange = (event) => {
        setText(event.target.value)
    }
    const [text, setText] = useState("");
    return (
        <>
            <div className="mb-3" style={{ color: props.mode === 'dark' ? 'white' : '#042743' }}>
                <h2>{props.heading}</h2>

                <textarea className="form-control texthere" rows="6" value={text} onChange={handleOnchange} style={{
                    backgroundColor: props.mode === 'dark' ? 'grey' : 'white',
                    color: props.mode === 'dark' ? 'white' : '#042743'
                }} placeholder="Enter Your Text"  ></textarea>

                {/* CONVERT TO UPPERCASE  */}

                <button className="upbtn btn btn-primary my-3" onClick={uppercase}>Convert To Upper Case</button>

                {/* CONVERT TO LOWERCASE  */}

                <button className="lowbtn btn btn-primary my-3 mx-4" onClick={lowercase}>Convert To Lower Case</button>

                <button className="lowbtn btn btn-primary my-3 mx-4" onClick={Clearall}>Clear All</button>

                <button className="lowbtn btn btn-primary my-3 mx-4" onClick={Extraspace}>Remove Extra Spaces</button>

                {/* FIRST LETTER CAPITALIZE  */}

                {/* <button className="btn btn-primary my-3 mx-4" onClick={titleCase}>Capitalize First Letter</button> */}

                <CopyToClipboard text={text}>
                    <button className="copybtn btn btn-secondary my-3 mx-4">Copy To Clipboard</button>
                </CopyToClipboard>

            </div>
            <div className="container numchar my-2" style={{ color: props.mode === 'dark' ? 'white' : '#042743' }} >
                <h6>{text.split(" ").length} Words and {text.length} Characters</h6>
            </div>
            <div className="container numchar my-3" style={{ color: props.mode === 'dark' ? 'white' : '#042743' }} >
                <h6> It may take average of {3.16 * text.split(" ").length} seconds to read this text</h6>
            </div>

            <div className="container my-4 preview" style={{ color: props.mode === 'dark' ? 'white' : '#042743' }} >
                <h3>Preview</h3>
                <div className="container my-3">
                    <p>{text.length > 0 ? text : "Enter Something To Preview Here"}</p>
                </div>

            </div>
        </>
    )
}
